<template>
    <div class='_tabTop'>
        <el-row class='meadio'>
        <el-col :span="15">
            <div class="grid-content bg-purple-dark _tabTopconent">
                <label class='label_elect selectone'>
                    <span>: </span>
                    <el-select v-model="form.cate_name" placeholder="" popper-class="treeSelect" @focus="showSelect()" ref="selectRef">
                      <el-option :value="valueModel" :label="labelModel">
                        <el-tree :data="data" node-key="id" :check-strictly="true" check-on-click-node auto-expand-parent default-expand-all :default-checked-keys= "[cheackId]" :props="defaultProps" @node-click="handleNodeClick" @check-change="handleClick" ref="treeForm"></el-tree>                          
                      </el-option>          
                    </el-select>
                </label>
                <label class='label_elect selectTwo'>
                    <span>: </span>
                    <el-select placeholder="" v-model='statisThis.form.smantype'>
                        <el-option label="" value="0"></el-option>
                        <el-option label="" value="1"></el-option>
                        <el-option label="" value="2"></el-option>
                    </el-select>
                </label>
                <label class='label_elect selectone'>
                    <span>: </span>
                    <el-select placeholder="" v-model='statisThis.form.smantype'>
                        <el-option :label="item.name" :value="item.id" v-for='item in form.smantypeArr' :key='item.id'></el-option>
                    </el-select>
                </label>
                <label class='label_elect selectone'>
                    <span>: </span>
                    <el-date-picker
                        v-model='statisThis.form.start_time'
                        type="date"
                        placeholder="">
                    </el-date-picker>
                </label>
                <label class='label_elect selectone' style='margin-left: 8px'>
                    <span></span>
                    <el-date-picker
                        v-model='statisThis.form.end_time'
                        type="date"
                        placeholder="">
                    </el-date-picker>
                </label>
                <label class='label_search'>
                    <input type="text" placeholder="" v-model='statisThis.form.goods_name'>
                    <i class='search_icon icon-width' @click='searchKec'></i>
                </label>
            </div>
        </el-col>
        <el-col :span="4">
            <section class='tabConenter'>
                <span>0</span>
                <span>0</span>
            </section>
        </el-col>
        <el-col :span="4">
            <div class="grid-content bg-purple-dark _tabTopconent" style=' justify-content: flex-end;'>
                <Lbutton class='hot_btn'>
                    <i class='icon-width export_icon'></i>
                    Excel
                </Lbutton>
                 <Lbutton class='hot_btn' >
                    <i class='icon-width refresh_icon'></i>
                    
                </Lbutton>
            </div>
        </el-col>
      </el-row>
    </div>
</template>
<script>
import Lbutton from '../../../../button/button'
import {cate_list} from '../../../../../getserver/getAxios'
import {mapState} from 'vuex'
export default {
  name: 'seaticTop',
  data () {
    return {
        valueModel:'',
        labelModel:'',
        cheackId:null,
        data: [],
        form: {
            name: '',
            id:null,
            cate_name:'',
            smantypeArr: [{id:1,name:""},{id:2,name:""},{id:3,name:''}]
        },
        defaultProps: {
          children: 'sub',
          label: 'cate_name'
        } 
    }
  },
  inject: ['statisThis'],
  computed: {
    ...mapState(['userLoginInfo'])
  },
  created(){
    this.getcate_list()
  },
  methods: {
    async getcate_list () {
        let resDate = await cate_list(this.userLoginInfo.orgid)
        if (resDate.data.status){
            this.data = resDate.data.data
        }
    },
    searchKec(){
      // 
    },
    handleNodeClick(data) {
      this.form.pid = data.cate_id
      this.cheackId = data.cate_id
      this.form.cate_name = data.cate_name
      this.$refs.treeForm.setCheckedNodes([data]);
      this.$refs.selectRef.blur()
    },
    handleClick(data, checked, node){
      if(checked == true){
        this.cheackId = data.id;
        this.$refs.treeForm.setCheckedNodes([data]);
        this.$refs.selectRef.blur()
      }
    },
    showSelect(){
      let _this = this
      if(this.cheackId){
        let node1 = document.querySelector('.treeSelect .is-current');
        setTimeout(function(){
          _this.$refs.treeForm.$parent.$parent.$el.children[0].scrollTop = node1.children[0].offsetTop-50
          // console.log(_this.$refs.treeForm.$parent.$parent)
          // console.log(_this.$refs.treeForm.$parent.$parent.$el.children[0].scrollTop)
          // console.log(node1.children[0].offsetTop)            
        },200)
      }
    }
  },
  components: {
    Lbutton: Lbutton
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../../style/mixin';
@import '../../../../../style/var';
input,button{
    border: 0;
    background: none;
}
._tabTop{
  @include wh(100%, auto);
  margin: 30px 0;
  .tabConenter{
    line-height: vw(40);
    padding-left: vw(50);
    box-sizing: border-box;
    font-size: vw(16);
    color: #333333;
    span:last-child{
        margin-left: vw(40);
    }
  }
  .icon-width{
    display: inline-table;
    @include wh(vw(24), vw(20));
  }
  ._tabTopconent{
      display: flex;
      justify-content: flex-start;
  }
  .label_search{
    // @include wh(140px, 40px);
    @include wh(vw(140), vw(40));
    display: flex;
    align-items: center;
    border: 1px solid;
    @include borderColor-defaul($border-color);
    padding-left: vw(12);
    padding-right: vw(16);
    box-sizing: border-box;
    border-radius: 6px;
    margin-left: vw(20);
    input {
        @include wh(80%, 100%);
        // flex: 1;
    }
  }
  .selectone{
    @include wh(vw(178), vw(40));
  }
  .selectTwo{
    @include wh(vw(198), vw(40));
  }
  .label_elect{
    display: inline-block;
    display: flex;
    align-items: center;
    font-size: 16px;
    color: #333333;
    margin-left: vw(20);
    .el-select,.el-date-editor{
        flex: 1;
        margin-left: 8px;
         @include wh(100%, 100%);
        /deep/ .el-input{
          @include wh(100%, 100%);
          .el-input__inner {
            @include wh(100%, 100%);
          }
        }
        .el-icon-arrow-up{
            display: flex;
            align-items: center;
        }
    }
    /deep/.el-date-editor{
        .el-input__inner{
            height: vw(40);
        }
        .el-input__prefix{
            display: none;
        }
    }

  }
  .label_elect /deep/ .el-select .el-input .el-input__inner{
    @include borderColor-defaul($border-color);
  }
 
  .hot_btn{
    @include wh(vw(128), vw(40));
    margin-left: vw(20);
    .icon-width{
        margin-right: vw(5);
    }
  }
} 
.treeSelect .el-scrollbar .el-select-dropdown__item{
  height:auto;
  padding:0
}
.treeSelect .el-scrollbar .el-select-dropdown__item .el-tree /deep/ .el-tree-node.is-checked>.el-tree-node__content{
  @include bg-defaul($bg-color);
  @include fontColor-defaul($bgColor-color)
}
.treeSelect .el-scrollbar .el-select-dropdown__item.selected{
  font-weight: 500
}
</style>