<template>
    <div class='table-gulp'>
    	<erp-tabtop @cate_index="goods_index" @orgid="32"></erp-tabtop>
        <section class='div_gulptable'>
            <el-table
            	ref="table"
                :data="tableData"
                border
                stripe
                resizable=false>
	            <el-table-column
	                prop="goods_name"
	                show-overflow-tooltip
	                align='center'
	                min-width='6.666%'
	                label="">
	                <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.goods_name}} </span></template>
              </el-table-column>
              <el-table-column
                 prop="cate_name"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="">
                 <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.cate_name}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="unit_name"
                 show-overflow-tooltip
                 align='center'
                 min-width='5.555%'
                 label="">
                 <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.unit_name}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sto_avg_money"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="()">
                 <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.sto_avg_money}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="dep_avg_money"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="()">
                 <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.dep_avg_money}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sgoods_amount"
                 show-overflow-tooltip
                 align='center'
                 min-width='5.555%'
                 label="">
                 <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.goods_amount}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sto_total_num"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="">
                 <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.sto_total_num}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="sto_total_money"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="()">
                 <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.sto_total_money}} </span></template>
              </el-table-column>              
              <el-table-column
                 prop="dep_total_num"
                 show-overflow-tooltip
                 align='center'
                 min-width='6.666%'
                 label="">
                 <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.dep_total_num}} </span></template>
              </el-table-column>              
	            <el-table-column
	            prop="dep_total_money"
	            show-overflow-tooltip
	            align='center'
	            min-width='6.666%'
	            label="()">
              <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.dep_total_money}} </span></template>
	            </el-table-column>	            
	            <el-table-column
	                prop="sale_total_money"
	                show-overflow-tooltip
	                align='center'
	                min-width='6.666%'
	                label="()">
                  <template slot-scope="scope"><span @click="layerOpen(scope.row,$event)">{{scope.row.sale_total_money}} </span></template>
	            </el-table-column>
	            <el-table-column
	                prop="remark"
	                show-overflow-tooltip
	                align='center'
	                min-width='6.666%'
	                label="">
	                <template slot-scope="scope">
	                	<span @click="layerOpen(scope.row,$event)">{{scope.row.remark}}</span>
	                </template>	                
	            </el-table-column> 
	            <el-table-column
	                label=""
	                min-width='22.222%'>
	                <template slot-scope="scope" class='erp-call'>
	                    <div class="grid-content bg-purple">
                        <el-button  type="text" @click="showJobstatus1(scope.row.goods_id)" size="small"  class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          
                        </el-button>                        
	                    	<el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon noIcon'>
	                    		<i class='icon-width'></i>
	                    		
	                    	</el-button>
                        <el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          
                        </el-button>                        
                        <el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          
                        </el-button>
                        <el-button @click="showAlert(scope.row, scope.$index)" type="text" size="small" class='erp-button-icon noIcon'>
                          <i class='icon-width'></i>
                          
                        </el-button>                                                	                    	
	                    	<el-button type="text" @click="showDelete(scope.row.goods_id)" size="small"  class='erp-button-icon noIcon'>
	                    		<i class='icon-width'></i>
	                    		
	                    	</el-button>                    	
	                    </div>
	                </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts="counts" :page_num="page_num" :pageSize="pageSize" @pagings='paging_father' v-if='last_page>1'></erp-paging>
        <editSalesman v-if="show" :that="that" @hideModal="hideModal" @submit="submit"></editSalesman>
        <deleteModal :show="showDe" :id="deleteId" @hideDeleteModal="hideDeleteModal" @submitDelete="submitDelete"></deleteModal>
    </div>
</template>
<script>
import $ from '../../../../script/jquery-vendor.js'
import layerOpen from '../../../../script/tableLayer.js'
import erpTabtop from './commodity-top/commodity-top.vue'
import erpPaging from '../../../paging/pagingShort'
import editSalesman from '../commodity/alert-box/editCommodity.vue'
import {goods_index,mans_del,mans_jobstatus1,mans_jobstatus2} from '../../../../getserver/getAxios' 
import {mapState} from 'vuex'
let that
export default {
	// ...tableLayer,
  name: 'salesman',
  data () {
    return {
      that: this,
      tableData: [],
		  addVisible: false,// 
		  value: '',
		  title: '',
		  show: false,
		  showDe:false,
		  deleteId:null,
      goods_name:'',//
      cate_id:null,//id
      counts:null,// 
      page_num:1,// 
      last_page:1,// 
      pageSize:8,//
      name:'',//
      id:'',//id
      phone:'',//
      status:1,
      delectType:'5',//02,3,4,5,6

    }
  },
  beforeCreate: function () {
  	that = this;
  },  
  computed:{
  	...mapState(['userLoginInfo']),
  },
  filters:{
  	serialNumber ( val ){
  		return (that.page_num-1)*8+(val + 1)
  	}
  }, 
  methods: {
    paging_father (n) {
      this.goods_index(n)
    },    
    layerOpen:function (a,b) {//
    	 layerOpen.layerOpen(a,b)	
    },
    async goods_index (n) {
    	let t = (new Date()).getTime();
    	let resDate = await goods_index(n,this.userLoginInfo.orgid,t,this.cate_id,this.goods_name)
    	if (resDate.data.status) {
    		this.tableData = resDate.data.data.data
    		this.last_page = resDate.data.data.last_page
    		this.counts = resDate.data.data.total
    		this.page_num = n;   		            
    	}
    },            
    mans_del (id) {
		// 
		mans_del(id,this.userLoginInfo.orgid).then((resDate)=>{
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				if((this.tableData.length -1) == 0){
					this.goods_index (this.page_num-1)
				}
				else{
					this.goods_index (this.page_num)
				}
			}					
    })
		},
		async jobstatus1 (id) {//
			let resDate = await mans_jobstatus1(id)
			if (resDate.data.status) {
				this.$message({
					type: 'success',
					message: resDate.data.sinfo
				});
				this.goods_index (this.page_num)
			}
		},
    async jobstatus2 (id) {//
    	let resDate = await mans_jobstatus2(id)
    	if (resDate.data.status) {
    		this.$message({
    			type: 'success',
    			message: resDate.data.sinfo
    		});
    		this.goods_index (this.page_num)
    	}
    }, 		        
    showAlert(a,b){
    	this.phone = a.sm_mobile
    	this.id = a.sm_id
      this.name = a.sm_name
      this.status = a.status
    	this.show = true
    },   
    hideModal() {
    // 	        
	    this.show = false
    },
    showDelete(id){
    	this.deleteId = id;
    	this.showDe = true
    },    
    hideDeleteModal(){
    	this.showDe = false
    },
    submitDelete(id){
    	if(this.delectType == "5"){
    		this.jobstatus1(id)
    	}
    	else if(this.delectType == "6"){
    		this.jobstatus2(id)
    	}        
    	else{
    		this.mans_del(id)
    	}        
    	this.showDe = false    	
    },
    submit() {
    //    
	    this.last_page = 0
        this.paging_father(this.page_num)
	    this.show = false
    },
    showJobstatus1(id){
    	console.log(id)
    	this.deleteId = id
    	this.delectType = "5"
    	this.showDe = true        
    },    
    showJobstatus2(id){
    	this.deleteId = id
    	this.delectType = "6"
    	this.showDe = true        
    },        
  }, 
  created: function () {
  	this.goods_index (1)
  },
  mounted(){
  	let h1 = $(".div_gulptable .el-table .has-gutter th").height()
  	$(".div_gulptable .el-table").height(h1*(this.pageSize+1)+8+"px")  	
  },
  components: {
  	erpTabtop: erpTabtop,
  	erpPaging: erpPaging,
  	editSalesman: editSalesman,
  }
}
</script>
<style lang="scss" scoped>
@import '../../../../style/mixin';
.div_gulptable{
    @include wh(100%,auto);
    background-color: #ffffff;
    box-shadow: 0px 2px 8px 0px rgba(12, 51, 110, 0.12);
}
.icon-width{
    @include wh(20px, 20px);
    display: inline-block;
    margin-right: vw(6);
    font-size: vw(16);
    color: #666666;
}
.grid-content{
    text-align: center;
    button:nth-child(2n){
        margin-right: vw(50);
        margin-left: vw(50);
    }
}
.el-pagination{
    padding-right: vw(60);
    margin-top: vw(60); 
}
.div_gulptable /deep/.erp-button-icon span{
	font-size:vw(16);
}
.noIcon{
	margin:0 vw(8)!important;
}
.noIcon .icon-width {
	width:1px
}
.el-table /deep/ th>.cell{
  padding:0;
  font-size:vw(16)
}
@media screen and(max-width: 1381px){
  .el-table /deep/ th>.cell{
    font-size:vw1(16)
  }
  .noIcon{
    margin:0 vw1(8)!important;
  }
}
</style>
