<template>
    <div>
      <seaticTop></seaticTop>
      <div class='table-gulp'>
        <!-- <erp-tabtop></erp-tabtop> -->
        <section class='div_gulptable'>
            <el-table
                :data="tableData"
                height="520"
                border
                stripe
                resizable=false
                style="width: 100%">
                <el-table-column
                    prop="cur_id"
                    show-overflow-tooltip
                    align='center'
                    min-width='13.33%'
                    label="">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="">
                    <template slot-scope="scope">
                        <span class='hot' @click='layerOpen(scope.row,$event)'>
                            {{scope.row.cur_name}}
                            <i class='hot_icon iconhot-width' v-if='scope.row.popular==1'></i>
                        </span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="subject.sname"
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="">
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='12.22%'
                    label="">
                    <template slot-scope="scope">
                        <span v-if='scope.row.tmethods==1'></span>
                        <span v-else-if='scope.row.tmethods==2'></span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="()">
                    <template slot-scope="scope">
                        <span>{{scope.row.ctime}}</span>
                    </template>
                </el-table-column>
                <el-table-column
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="">
                    <template slot-scope="scope">
                        <span v-if='scope.row.status==1'></span>
                        <span v-else-if='scope.row.status==1'></span>
                    </template>
                </el-table-column>
                <el-table-column
                    prop="address"
                    show-overflow-tooltip
                    align='center'
                    min-width='14.44%'
                    label="">
                    <template slot-scope="scope">
                        <span v-if='scope.row.state==1'></span>
                        <span v-else-if='scope.row.state==2'></span>
                    </template>
                </el-table-column>
            </el-table>
        </section>
        <erp-paging :counts='countNum' :page_num='page_num' @pagings='paging_father'  v-if='countNum>1'></erp-paging>
    </div>
    </div>
</template>
<script>
import seaticTop from './seaticTop/seaticTop'
import {getSaleList} from '../../../../getserver/server'
import {mapState} from 'vuex'
export default {
  name: 'statistical',
  data () {
    return {
      tableData: [],
      countNum: 1,
      form: {
        cate: "",
        smantype: '',
        timetype: '',
        goodsname: '',
        start_time: '',
        end_time: '',

      }
    }
  },
  provide(){
    return {
      statisThis: this
    }
  },
  computed:{
      ...mapState(['userLoginInfo'])
  },
  created(){
    this.getSaleList_fun();
  },
  methods: {
    async getSaleList_fun () {
        let resDate = await getSaleList(this.userLoginInfo.orgid,"","","","","","",this.countNum,10);
        if (resDate.data.status) {
            console.log(resDate)
        }
    }
  },
  components: {
    seaticTop: seaticTop
  }
}
</script>
<style lang="scss" scope>
    
</style>