export default {
	layerOpen:function(a,b) {
		var pt = $(b.path[1]).width();
		var t = b.currentTarget.getBoundingClientRect();
		if (pt < t.width) {
			var that = b.currentTarget;
			var text = b.path[0].innerText;
			var l = t.x-10+'px';
			var t = t.y-10+'px';
			layer.open({
				type: 1,
				fixed: false,
				icon: 2, 
				offset: [t,l],   //
				shade: false,
				title: false, //
				area: [pt, 'auto'],
				content: text, //body
				success: function(layero,index){
				    layero.css({"min-width":pt,"padding":"10px","background-color":"#fff"}).append(layero);    //
				  }
				});
		}      	
	},
}
